import { Component, ViewChild, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams, Slides } from 'ionic-angular';

import { Storage } from '@ionic/storage';

import { SideMenuControllerService } from './../../../services/side-menu-controller.service';
import { ClubService } from './../../../services/club.service';

import { Club } from './../../../models/club.model';


@IonicPage()
@Component({
    selector: 'page-club-profile',
    templateUrl: 'club-profile.html',
})
export class ClubProfilePage implements OnInit {

    @ViewChild(Slides) gallery: Slides;
    isBeginning = true;
    isEnd = false;

    clubKey: string;
    club: Club = new Club();

    constructor(
        public navCtrl: NavController,
        public navParams: NavParams,
        public sideMenuControllerService: SideMenuControllerService,
        private clubService: ClubService,
        private storage: Storage
    ) {
        this.sideMenuControllerService.setupClubSideMenuContent();
        this.clubKey = this.navParams.get('clubKey');
    }

    ngOnInit() {

        if (!this.clubKey || this.clubKey === undefined) {
            console.log('its the club admin data exit in locale storage');
            this.storage.get('currentClub').then(
                (response) => {
                    this.club = response;
                    console.log(this.club);
                },
                (error) => {
                    console.log(error);
                }
            );
        } else {
            console.log('its a public visitor, club id must be passed with nav params');
            this.clubService.getClubById(this.clubKey).subscribe(
                (response) => {
                    this.club = response;
                    console.log(this.club);
                },
                (error) => {
                    console.log(error);
                }
            );
        }

    }

    ionViewDidLoad() {
        
    }

    slideChanged() {
        this.isBeginning = this.gallery.isBeginning();
        this.isEnd = this.gallery.isEnd();
    }

    nextSlide() {
        this.gallery.slideNext();
    }

    prevSlide() {
        this.gallery.slidePrev();
    }

}



